<template>
<div class="about_wrap">
    <mt-tab-container v-model="active" swipeable class="container">
      <mt-tab-container-item id="tab-container1" class="one_wrap">
       <span class="tag_designTime">{{tag}}</span>
       <span class="name" role="button">{{name}}</span>
       <div class="project_wrap " >
          <ul>
            <li>
              <h5>停车场信息采集系统WebApp全栈开发<span>&nbsp;&nbsp;&nbsp;个人</span></h5>
              <p>&nbsp;&nbsp;项目简介：实现停车场实时信息展现在App上，用户可注册登录查看自己的停车位置，停车时间和消费情况，增强与用户交互性，App使用的流畅性，把停车场信息实时展现在App上。</p>	
              <p>&nbsp;&nbsp;工作职能：前端主要使用mui框架和div+css+javascript Dom操作进行App的开发，使用预加载页面实现跳转页面的流畅性，使用Ajax实现异步与后台的数据传输。后台使用JAVA+Mysql+Tomcat MVC模式构建服务器。</p>			
            </li>
            <li>
              <h5>基于安卓图片美化App全栈开发<span>&nbsp;&nbsp;&nbsp;个人</span></h5>
              <p>&nbsp;&nbsp;项目简介：用户可对自己图库的照片进行对比度、亮度、色相等属性修改，并有有怀旧、黑白等四种滤镜选择。还可以从云端公共图库下载图片至本机。</p>	
              <p>&nbsp;&nbsp;工作职能：XML实现前端的设计布局， animate实现动画的旋转，通过Java操作图片的颜色矩阵，实现图片的各种样式。运用云端服务器Bmob，实现数据和图片的异步存储和传输。</p>	
            </li>
            <li>
              <h5>新闻中心后台管理<span>&nbsp;&nbsp;&nbsp;组员</span></h5>
              <p>&nbsp;&nbsp;项目简介：管理员的登录，新闻内容的增删改查。</p>	
              <p>&nbsp;&nbsp;工作职能：JSP实现数据与后台的传输（增、删、改、查），html+css实现页面的布局，Java+Mysql+Tomcat实现数据库的搭建。
              </p>	
            </li>
            <li>
              <h5>基于JS canvas 动画实现<span>&nbsp;&nbsp;&nbsp;</span></h5>
              <p>跳动时钟效果、贪吃鱼游戏、放大镜图片效果</p>		
            </li>
          </ul>
        </div>
      </mt-tab-container-item>

      <mt-tab-container-item id="tab-container2" class="two_wrap">
       <span class="tag_designTime">{{tag}}</span>
        <div class="content_wrap">
          <span class="name" role="button">{{name}}</span>
        </div>
         <Me></Me>
      </mt-tab-container-item>

      <mt-tab-container-item id="tab-container3" class="three_wrap">
       <span class="tag_designTime">{{tag}}</span>
       <span class="name" role="button">{{name}}</span>
    </mt-tab-container-item>
    </mt-tab-container>
</div>
</template>
<script>
import Me from './Me.vue'
import Monitor from '../assets/js/pageMonitor.js'
    export default{
      data(){
        return{
          active: 'tab-container2',
          tag: 'DESIGN WORK 2017',
          name: 'ZHANG YU'
        }
      },
      beforeCreate(){
          Monitor.browserRedirect();
      },
      components:{
        Me
      }
    }

</script>
<style lang="less" scoped>
  .about_wrap{
    height: 95%;
    border: 1px solid yellow;
    background-color: black;
    overflow: hidden;
    
    .container{
      height: 80%;
      margin-top: 3rem; 
      overflow: auto;
    }
    .one_wrap{
      
      background-image: url(../assets/images/left_page.jpg);
      background-size: cover;
      overflow: hidden;
    }
    .two_wrap{
      background-image: url(../assets/images/bg_3.jpg);
      background-size: cover;
      overflow: auto;
      .content_wrap{
      }
    }
     .three_wrap{
      background-image: url(../assets/images/left_page.jpg);
      background-size: cover;
      overflow: hidden;
    }
    .tag_designTime{
      width: 11rem;
      display: block;
      background: transparent;
      color: white;
      font-size: 1.5rem;
      margin: 5rem auto;
      text-align: center;  
    }
    .name{
      display: block;
      width: 13rem;
      margin: 10rem auto;
      border: solid 1px transparent;
      font-size: 2rem;
      color: white;
      background: transparent;
      text-align: center;
      transition: all 0.5s ease;
      &:hover{
        border: solid 1px yellow;
	      background: rgba(0,0,0,0.6);
      }
    }
  }
</style>



