<template>
  <div id="app">
    <router-view/>
    <mt-tabbar v-model="selected" fixed>
      <mt-tab-item id="About" @click.native="gotoRouter">
        <img slot="icon" src="./assets/images/logo.png">
        About
      </mt-tab-item>
      <mt-tab-item id="Learn" @click.native="gotoRouter">
        <img slot="icon" src="./assets/images/logo.png">
        Learn Log
      </mt-tab-item>
      <mt-tab-item id="Contact" @click.native="gotoRouter">
        <img slot="icon" src=".\assets\images/logo.png">
        contact
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>

import { Toast } from 'mint-ui';
import Monitor from './assets/js/pageMonitor.js'
export default {
  name: 'app',
  data(){
    return {
      selected:''
    }
  },
  beforeCreate(){
      Monitor.browserRedirect();
  },
  methods:{
    gotoRouter(e){
     Toast({
        message: '提示',
        position: 'middle',
        duration: 5000
      });
      console.log(this.selected)
      this.$router.push(this.selected);
    },
  }

}
</script>

<style>
*{
	font-family: "微软雅黑";
}
#app {
 margin: 0px;
 padding: 0px;
 width: 100%;
 height: 100%;
 overflow: auto;
}
html,body{
  margin: 0px;
  padding: 0px;
  width: 100%;
  height: 100%;
  background: black;
}
</style>



